body {
    background-color: rgb(255, 255, 255)
}

.content {
    width: 80%;
    margin: 0 auto;
}

h1 {
    text-align: center;
}

#people {
    width: 100%;
    border-collapse: collapse;
    text-align: center;
    padding: 8px;
}

#people tr:nth-child(even) {
    background-color: #d89292;
}

#people tr:hover {
    background-color: rgb(230, 216, 216);
}

#people th {
    background-color: rgb(93, 88, 88);
    color: #fff;
}

#people th, #people td {
    border: 1px solid rgb(226, 215, 215);
    padding: 8px;
}

.success {
    background-color: green;
    color: #fff;
    padding: 10px;
    border: 2px solid rgb(206, 230, 71);
}
  
.error {
    background-color: rgb(184, 25, 25);
    color: #fff;
    padding: 10px;
    border: 2px solid rgb(56, 8, 48);
}